












































































































































.box {
    width: 100%;
    height: 100vh;
    min-height: 700px;
    display: flex;
    .login-left {
        width: 65%;
        height: 100%;
        background-image: url(../../src/assets/images/login.png);
        background-size: 100% 100%;
        overflow: hidden;
    }
    .login-right {
        width: 35%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        .login-box {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            .login-titel {
                text-align: center;
                font-size: 20px;
                font-weight: 600;
                color: #3e3e3e;
                span {
                    font-size: 12px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: #b7b7b7;
                }
            }
            .login-content {
                margin-top: 200px;
            }
        }

        .right-footer {
            width: 100%;
            max-width: 358px;
        }
        .more-box {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        .more-box span {
            font-size: 12px;
            font-weight: 400;
            color: #333333;
            line-height: 17px;
        }
        .more-hr {
            height: 1px;
            width: 120px;
            background: #d8d8d8;
        }
        .more-item {
            margin-top: 24px;
            width: 100%;
            display: flex;
            justify-content: space-between;
        }
        .more-item > .item {
            cursor: pointer;
            text-align: center;
            font-size: 12px;
            font-weight: 400;
            color: #999999;
            line-height: 17px;
        }
        .more-item > .item > .item-img {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: auto;
            width: 40px;
            height: 40px;
            border-radius: 4px;
            margin-bottom: 10px;
            border: 1px solid #999999;
        }
        .item-img img {
            width: 20px;
            height: 20px;
        }
    }
}
